* {
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  background: radial-gradient(#11112b, #0e0e2b) 0 0 / cover no-repeat;
  margin: 0;
  font-family: Cambria, Cochin, Georgia, Times, Times New Roman, serif;
}

img {
  max-width: 100%;
}

.header-section {
  background-color: #6e6d80;
  border-bottom: 2px solid #141429;
  flex-direction: column;
  align-items: center;
  margin: 0 0 2em;
  padding: 1em 0 2em;
  display: flex;
  box-shadow: 2px 2px 2px #000;
}

.main-heading {
  color: #050531;
  text-shadow: 1px 1px 1px #000;
  margin: .5em 0;
  font-size: 2.3rem;
}

.search-form {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-label {
  font-size: 1.5rem;
}

#search-input {
  text-align: center;
  border: none;
  border-radius: 10px;
  margin: 0 1em;
  padding: .5em;
  font-size: 1.1rem;
}

.search-button {
  color: #080822;
  cursor: pointer;
  background-color: #c7b7b7;
  border: 1px solid #080822;
  border-radius: 10px;
  padding: .4em .7em;
  font-size: 1rem;
  font-weight: 700;
}

.search-button:focus, .search-button:hover {
  background-color: #aa9b9b;
}

.results-container {
  flex-wrap: wrap;
  gap: 1em;
  margin: 1em;
  display: flex;
}

.movie-container {
  width: 10em;
  text-align: center;
  background-color: #7e7d8b;
  border: 1px solid #000;
  border-radius: 10px;
  padding: 1em;
}

.movie-title {
  margin: 0;
}

.movie-year {
  margin: .5em 0;
  font-size: 1.4rem;
}

.footer-section {
  justify-content: center;
  margin: 1em;
  padding: 1em 0;
  display: flex;
}

.error-text {
  color: #a50606;
  background-color: #fff;
  padding: 1em;
}

